'use strict';

Object.defineProperty(exports, '__esModule', { value: true });

class Adaptive {
  constructor() {
    this.options = {
      // 1rem = 16px
      rootValue: 16,
      // px
      designWidth: 750,
      // px
      maxWidth: 1024
    };
    this.meta = document.getElementsByName('viewport')[0];

    if (!this.meta) {
      //  viewport meta  meta
      this.meta = document.createElement('meta');
      this.meta.name = 'viewport';
      this.meta.content = this.createViewportContent('1.0');
      document.head.append(this.meta);
    } else if (!this.meta.content) {
      //  content  scale  1
      this.meta.content = this.createViewportContent('1.0');
    } //  content


    this.metaDefualtContent = this.meta.content;
  }

  run() {
    //  body 
    this.setBodyWidth(); // CSS1  designWidth   scale hmtl

    if (window.devicePixelRatio > 1 && (document.documentElement.clientWidth < this.options.designWidth || document.documentElement.clientHeight < this.options.designWidth)) {
      // 
      const resize = () => {
        // 
        window.removeEventListener('resize', resize); // console.log('resize')
        //  scale

        this.setViewportScale().then(() => {
          //  html 
          this.setHtmlFontSize(); // 

          window.addEventListener('resize', resize);
        });
      };

      resize();
    } else {
      this.setHtmlFontSize(); //  scale  html 

      window.addEventListener('resize', () => {
        this.setHtmlFontSize();
      });
    }
  }
  /**
   * 
   * @param {number|string} rootValue   - 1rem  px16
   * @param {number|string} designWidth - px750
   * @param {number|string} maxWidth    - px1024
   */


  config({
    rootValue,
    designWidth,
    maxWidth
  }) {
    this.options.rootValue = this.options.rootValue || rootValue;
    this.options.designWidth = this.options.designWidth || designWidth;
    this.options.maxWidth = this.options.maxWidth || maxWidth;
    return this;
  } //  meta[viewport] scale


  setViewportScale() {
    return new Promise(resolve => {
      this.revertViewportContent().then(() => {
        let scale = document.documentElement.clientWidth / this.options.designWidth; //  html font-size 

        if (scale >= 1) {
          scale = '1.0';
        }

        this.delayUpdate(this.metaDefualtContent.replace(/(scale=).+?(,)/g, `$1${scale}$2`), resolve);
      });
    });
  } //  mate.content


  revertViewportContent() {
    return new Promise(resolve => this.delayUpdate(this.metaDefualtContent, resolve));
  } //  body 


  setBodyWidth() {
    document.body.style.width = `${this.options.designWidth / 16}rem`;
  } //  html 


  setHtmlFontSize() {
    // 
    let clientWidth = document.documentElement.clientWidth; // 

    if (clientWidth > this.options.maxWidth) {
      clientWidth = this.options.maxWidth;
    } // fontSize / pxToRemRootValue = clientWidth / designWidth


    let fontSize = clientWidth / this.options.designWidth * this.options.rootValue; // 12chrome12px

    if (fontSize < 12) {
      fontSize = 12;
    }

    document.documentElement.style.fontSize = fontSize + 'px';
  }
  /**
   *  meat[viewport].content
   * @param {number|string} scale - scale <= 1 && scale > 0
   */


  createViewportContent(scale) {
    return `width=device-width, initial-scale=${scale}, maximum-scale=${scale}, minimum-scale=${scale}, user-scalable=no`;
  } //  document.documentElement.clientWidth 


  delayUpdate(mateContent, resolve) {
    // console.log(document.documentElement.clientWidth, mateContent)
    if (this.meta.content === mateContent) {
      resolve();
      return;
    }

    const oldClientWidth = document.documentElement.clientWidth;
    this.meta.content = mateContent;

    const delay = () => {
      delay.count = delay.count + 1 || 1; // console.log(delay.count, oldClientWidth, document.documentElement.clientWidth)

      oldClientWidth === document.documentElement.clientWidth && delay.count < 10 ? window.setTimeout(delay, 100) : resolve();
    };

    delay();
  }

}

var main = new Adaptive();

exports.default = main;
