<template>
  <div class="image-scroll">
    <div class="images"
      :class="{ move: move, 'up-move': upMove, 'no-animation': noAnimation }"
      :style="{ width: items.length + '00%' }"
      ref="images">
      <a class="image" v-for="(item, i) in items" :key="i" :style="{ order: item.order, height: height }" :href="item.href">
        <img :src="item.imgUrl"/>
      </a>
    </div>
    <div v-if="pagination" class="pagination">
      <div class="point" v-for="(item, i) in items" :key="i" :class="{ active: i === index }"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ImageScroll',
  props: {
    images: Array,
    height: String,
    pagination: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      items: [ ...this.images ],
      // 
      index: 0,
      // 
      isStop: false,
      //  flex order 
      move: true,
      // 
      upMove: false,
      // 
      noAnimation: false
    }
  },
  created() {
    // 
    this.correct()
  },
  mounted () {
    if (this.images.length < 2) {
      this.move = false
      return
    }

    // 
    this.start.timeout = setTimeout(this.start, 3000)

    // X
    let startX
    // 
    const touchstart = evt => {
      // 
      this.stopTime()
      // 
      this.isStop = true
      // 
      this.noAnimation = true
      // 
      startX = evt.changedTouches[0].pageX
    }

    // 
    const touchend = evt => {
      // 
      removeEvent()
      // 
      this.stopTime()

      if (evt.changedTouches[0].pageX <= startX) {
        this.move = false
        this.$refs.images.style.transform = `translateX(${ evt.changedTouches[0].pageX - startX }px)`
          this.index ++
          if (this.index >= this.items.length) {
            this.index = 0
          }
          this.correct()
          .then(() => {
            // 
            this.noAnimation = false
          this.move = true
          this.$refs.images.style.transform = ``
            // 
            this.isStop = false
            // 
            this.start.timeout = setTimeout(this.start, 3000)
            // 
            addEvent()
          })
      } else {
        // 
        this.noAnimation = false
        //  style.transform  up-move 
        this.$refs.images.style.transform = ''
        // 
        this.up()
        // 
        .then(addEvent)
      }
    }

    // 
    const touchmove = evt => {
      // 
      this.stopTime()
      // 
      this.isStop = true
      // 
      this.noAnimation = true
      this.$refs.images.style.transform = `translateX(${ evt.changedTouches[0].pageX - startX - 750 }px)`
    }

    const addEvent = () => {
      this.$refs.images.addEventListener('touchstart', touchstart)
      this.$refs.images.addEventListener('touchend', touchend)
      this.$refs.images.addEventListener('touchmove', touchmove)
    }
    const removeEvent = () => {
      this.$refs.images.removeEventListener('touchstart', touchstart)
      this.$refs.images.removeEventListener('touchend', touchend)
      this.$refs.images.removeEventListener('touchmove', touchmove)
    }
    addEvent()
  },
  methods: {
    // 
    stopTime () {
      clearTimeout(this.correct.timeout)
      clearTimeout(this.start.timeout)
      clearTimeout(this.nextOrder.timeout)
      clearTimeout(this.up.timeout)
    },
    // 
    correct () {
      // 
      this.stopTime()
      return new Promise(resolve => {
        // 1
        this.items[this.index].order = 1

        // 
        let before = this.index - 1
        if (before < 0) {
          // 
          before = this.items.length - 1
          this.items[before].order = 0
        } else {
          // 
          this.items[before].order = 1
        }

        this.items.forEach((item, index) => {
          if (index === before || index === this.index) {
            return
          }
          if (index > this.index) {
            // 1
            item.order = 1
          } else {
            // 
            item.order = 2
          }
        })
        // Vue 
        this.items = [].concat(this.items)
        // 
        this.correct.timeout = setTimeout(resolve, 100)
      })
    },
    // 
    start () {
      // 
      this.stopTime()

      if (this.isStop) {
        return
      }

      // 
      this.nextOrder()
      .then(() => {
        // 
        this.index ++
        if (this.index >= this.items.length) {
          this.index = 0
        }
        this.move = true

        this.start.timeout = setTimeout(this.start, 3000)
      })
    },
    // 
    nextOrder () {
      // 
      this.stopTime()
      return new Promise(resolve => {
        // 
        this.correct()
        .then(() => {
          // 
          let before = this.index - 1
          if (before < 0) {
            before = this.items.length - 1
          }
          // move
          this.items[before].order ++
          this.move = false

          // Vue 
          this.items = [].concat(this.items)
          // 
          this.nextOrder.timeout = setTimeout(resolve, 100)
        })
      })
    },
    // 
    up () {
      // 
      this.stopTime()
      return new Promise (resolve => {
        // 
        this.upMove = true
        // 
        this.noAnimation = false

        this.up.timeout = setTimeout(() => {
          // 
          this.index --
          if (this.index < 0) {
            this.index = this.items.length - 1
          }
          // 
          this.upMove = false
          this.noAnimation = true
          this.correct()
          .then(() => {
            // 
            this.noAnimation = false
            // 
            this.isStop = false
            this.start.timeout = setTimeout(this.start, 3000)
            resolve()
          })
        }, 500)
      })
    }
  }
}
</script>

<style scoped>
.image-scroll {
  position: relative;
  width: 750px;
  overflow: hidden;
  z-index: 1;
}
.images {
  display: flex;
  transform: translateX(0);
}
.move {
  transition: transform 500ms ease;
  transform: translateX(-750px);
}
.up-move {
  transform: translateX(0);
}
.no-animation {
  transition: none;
}
.image {
  padding: 0 24px;
  width: 750px;
  height: 292px;
}
img {
  width: 100%;
  height: 100%;
}
.pagination {
  position: absolute;
  bottom: 20px;
  display: flex;
  justify-content: center;
  height: 4px;
  width: 100%;
}
.point {
  margin: 0 5px;
  width: 30px;
  height: 4px;
  background-color: rgba(255, 255, 255, 0.3);
}
.point.active {
  background-color: rgba(255, 255, 255, 0.6);
}
</style>
